<template>
  <div class="flex justify-around flex-col sm:flex-row" :class="{'bg-slate-200': isPause}">
    <div>
      <ClockComponent class="mx-auto w-[80vw] h-[80vw] sm:w-[60vw] sm:h-[60vw]" :displaying-time="displayingTime"/>
      <button class="w-[80vw] sm:w-[60vw] py-2 my-3 bg-red-500 text-bold text-md text-white shadow-inner" :class="{'bg-blue-500': isPause}" @click="isPause?resume():pause()">{{ isPause ? '시작' : "일시정지" }}</button>
    </div>
    <details>
      <summary>
        시간 선택하기
      </summary>
      <div class="flex flex-col">
        <button class="bg-white m-1 p-2 rounded-xl border text-left">
          <div class="text-lg font-bold" @click="toCurrentTime()">
            현재시간
          </div>
        </button>
        <button class="bg-white m-1 p-2 rounded-xl border text-left" v-for="item in timetable" :key="item" @click="onTimeClicked(item)">
          <div class="text-lg font-bold">
            {{ item.title }}
          </div>
          <div class="text-sm">
            {{ formatTime(item.start_time) }} ~ {{ formatTime(item.end_time) }}
          </div>
        </button>
      </div>
    </details>
  </div>

</template>

<script>

import ClockComponent from "@/components/ClockComponent";
export default {
  name: 'App',
  components: {
    ClockComponent
  },
  data(){
    return {
      nowTime: new Date(),        // 현재시각
      startTime: new Date(),      // 타이머가 시작된 시각
      offset: new Date(),         // 시계 기준시간
      displayingTime: new Date(), // 보여지는 시간
      isPause: false,
      timetable: [
        {
          title: '국어',
          start_time: new Date(1970, 0, 1, 8, 40),
          end_time: new Date(1970, 0, 1, 10, 0),
          period: 1,
        },
        {
          title: '수학',
          start_time: new Date(1970, 0, 1, 10, 30),
          end_time: new Date(1970, 0, 1, 12, 10),
          period: 2,
        },
        {
          title: '영어',
          start_time: new Date(1970, 0, 1, 13, 10),
          end_time: new Date(1970, 0, 1, 14, 20),
          period: 3,
        },
        {
          title: '영어(듣기 이후)',
          start_time: new Date(1970, 0, 1, 13, 35),
          end_time: new Date(1970, 0, 1, 14, 20),
          period: 3,
        },
        {
          title: '한국사',
          start_time: new Date(1970, 0, 1, 14, 50),
          end_time: new Date(1970, 0, 1, 15, 20),
          period: 4,
        },
        {
          title: '탐구 제1선택',
          start_time: new Date(1970, 0, 1, 15, 30),
          end_time: new Date(1970, 0, 1, 16, 0),
          period: 4,
        },
        {
          title: '탐구 제2선택',
          start_time: new Date(1970, 0, 1, 16, 2),
          end_time: new Date(1970, 0, 1, 16, 32),
          period: 4,
        },
        {
          title: '제2외국어',
          start_time: new Date(1970, 0, 1, 17, 0),
          end_time: new Date(1970, 0, 1, 17, 40),
          period: 5,
        },
      ]
    }
  },
  methods: {
    OnUpdate(){
      this.nowTime = new Date()
      this.displayingTime = new Date(this.offset.getTime() + this.nowTime.getTime() - this.startTime.getTime())
    },
    onTimeClicked(item){
      this.offset = item.start_time
      this.startTime = new Date()
      this.OnUpdate()
    },
    toCurrentTime(){
      this.resume()
      this.nowTime = new Date()
      this.startTime = new Date()
      this.offset = new Date()
      this.OnUpdate()
    },
    formatTime(time){
      if (time.getMinutes() === 0)
        return `${time.getHours()}시`
      else return `${time.getHours()}시 ${time.getMinutes()}분`
    },
    pause(){
      this.isPause = true
      this.offset = this.displayingTime
      if (this.timer != null)
        clearInterval(this.timer)
    },
    resume(){
      this.isPause = false
      this.startTime = new Date()
      if (this.timer != null)
        clearInterval(this.timer)
      this.timer = setInterval(this.OnUpdate, 1000)
    }
  },
  mounted() {
    if (this.timer != null)
      clearInterval(this.timer)
    this.timer = setInterval(this.OnUpdate, 1000)
  },
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>
