<template>
  <div class="relative">
    <img class="absolute w-full" src="../assets/clock.png" alt="시계"/>
    <div class="absolute w-[1%] h-[40%] top-[10%] left-[50%] origin-bottom bg-red-500 transform translate-x-[-50%]" :style="`transform: rotate(${displayingTime.getSeconds()*6}deg)`"/>
    <div class="absolute w-[1.5%] h-[35%] top-[15%] left-[50%] origin-bottom bg-slate-800 transform translate-x-[-50%]" :style="`transform: rotate(${displayingTime.getMinutes()*6 + displayingTime.getSeconds()*0.1}deg)`"/>
    <div class="absolute w-[2.5%] h-[25%] top-[25%] left-[50%] origin-bottom bg-slate-900 transform translate-x-[-50%]" :style="`transform: rotate(${displayingTime.getHours()*30 + displayingTime.getMinutes()*0.5}deg)`"/>
  </div>

</template>

<script>
export default {
  name: "ClockComponent",
  props: {
    displayingTime: {
      type: Object,
      default: new Date(),
    }
  }
}
</script>

